    }
  },
  methods: {
    userCheck() {
      const self = this;
      axios.post('/userChcek', this.info.id, {
        headers: {
          'Content-Type': 'application/json',
        }
      }).then(function (resp) {
        self.info = resp
        if (resp.status) {
          // 디비에 등록된 사용자면 true
        } else {
          //디비에 등록된 사용자가 아니면
        }
      })
    },
  }
}
</script>

